.gpt3__cta{
    display: flex;
    background: var(--gradient-bar);
    align-items: center;
    justify-content: space-between;
    padding: 2rem;
    margin: 3rem;
    border-radius: 1rem;
}

.gpt3__cta-contant p {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 30px;

    color: #0E0E0E;
}
.gpt3__cta-contant h3 {
    font-family: var(--font-family);
    font-size: 24px;
    font-weight: 800;
    line-height: 45px;

    color: #000000;

}

.gpt3__cta-button {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 2rem;
}

.gpt3__cta-button button {
    cursor: pointer;
    border-radius: 40px;
    font-family: var(--font-family);
    font-size: 18px;
    font-weight: 700;
    line-height: 30px;
    color: #FFFFFF;
    background: #000000;

    padding: 0.5rem 1rem;
    border: none;
    outline: none;
    min-width: 150px;
}

@media screen and (max-width: 650px) {
    .gpt3__cta{
        flex-direction: column;
    }
    .gpt3__cta-button {
        margin: 2rem 0 0;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__cta {
        margin: 4rem 2rem;
    }

    .gpt3__cta-contant h3 {
        font-size: 18px;
        line-height: 32px;
    }

    .gpt3__cta-button button {
        font-size: 14px;
        line-height: 28px;
    }
}