.gpt3__blog {
    display: flex;
    flex-direction: column;
}
.gpt3__blog-heading {
    width: 100%;
    text-align: left;

    margin-bottom: 5rem;
}
.gpt3__blog-heading h1 {
    font-family: var(--font-family);
    font-size: 62px;
    font-weight: 800;
    line-height: 75px;
}
.gpt3__blog-container {
    display: flex;
}
.gpt3__blog-container_groupA {
    .gpt3__blog-container_article-content h3 {
        margin-bottom: 14rem;
    }

    flex: 0.75;
    margin-right: 2rem;
}

.gpt3__blog-container_groupA p {
        margin-top: 2rem;
}

.gpt3__blog-container_groupB {
    flex: 1;

    grid-template-columns: repeat(2, 1fr);
    display: grid;
    grid-gap: 2rem;
}

@media screen and (max-width:950px) {
    .gpt3__blog-container {
        flex-direction: column-reverse;
    }

    .gpt3__blog-container_groupA {
        margin: 2rem 0;
    }

    .gpt3__blog-container_groupA .gpt3__blog-container_article{
        width: 48%;
    }
    
    .gpt3__blog-container_groupA {
        .gpt3__blog-container_article-content h3 {
        margin-bottom: 3rem;
        }
        .gpt3__blog-container_article-content p {
            margin-top: 0.3rem;
        }
    }
    
    .gpt3__blog-container_groupA .gpt3__blog-container_article-image{
        height: 220px;
        width: 100%;
    }
}
@media screen and (max-width:700px) {
    .gpt3__blog-container_groupB {
        grid-template-columns: repeat(1,1fr);
    }
    .gpt3__blog-container_groupA .gpt3__blog-container_article {
        width: 100%;
    }

    .gpt3__blog-heading h1 {
        font-size: 46px;
        line-height: 52px;
    }
}

@media screen and (max-width: 550px) {
    .gpt3__blog-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }
}
