.gpt3__blog-container_article {
    width: 100%;
    height: 100%;
    
    display: flex;
    flex-direction: column;
    background: var(--color-footer);
}
.gpt3__blog-container_article-img {
    width: 100%;
    height: 100%;
    background: var(--color-background);
}
.gpt3__blog-container_article-img img {
    width: 100%;
    height: 100%;
}
.gpt3__blog_article-content {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    padding: 1rem 1.5rem;
    height: 100%;
}
.gpt3__blog-container_article-content p {
    font-family: var(--font-family);
    font-size: 11.65px;
    font-weight: 700;
    line-height: 34.95px;

    margin: 0 1rem;

    color: #FFFFFF;
}
.gpt3__blog-container_article-content h3 {
    font-family: var(--font-family);
    font-size: 25.11px;
    font-weight: 800;
    line-height: 30.29px;
    text-align: left;

    margin: 0 1rem;

    color: #FFFFFF;
    margin-bottom: 3rem;
    cursor: pointer;
}
.gpt3__blog-container_article-content p:last-child {
    color: #FFFFFF;
    cursor: pointer;
}

@media screen and (max-width: 550px) {
    .gpt3__blog-container_article-content h3 {
        font-size: 18px;
        line-height: 25px;
    }
}